import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { Button, Row, Col, ListGroup, Image, Card } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../components/Message'
import CheckoutSteps from '../components/CheckoutSteps'
import { createOrder } from '../store/actions/orderActions'
import { ORDER_CREATE_RESET } from '../store/constants/orderConstants'
import { USER_DETAILS_RESET } from '../store/constants/userConstants'
import { resetCart } from '../store/cart/cartSlice'

const Spinner = () => (
	<span
		className='spinner-border spinner-border-sm mx-1'
		role='status'
		aria-hidden='true'></span>
)

const PlaceOrderScreen = ({ history }) => {
	const dispatch = useDispatch()

	const cart = useSelector(state => state.cart)

	// fake submit handlers
	const [loading, setLoading] = useState(false)
	const [success, setSuccess] = useState(false)

	if (!cart.shippingAddress.address) {
		history.push('/shipping')
		// } else if (!cart.paymentMethod) {
		// 	history.push('/payment')
	}

	//  * Calculate prices
	const addDecimals = num => {
		return (Math.round(num * 100) / 100).toFixed(2)
	}

	// cart.itemsPrice = addDecimals(
	// 	cart.cartItems.reduce((acc, item) => acc + item.price * item.qty, 0)
	// )

	// * Calculate total price for cart items
	const itemsPrice = addDecimals(
		cart.cartItems.reduce((acc, item) => acc + item.price * item.qty, 0)
	)

	const shippingPrice = addDecimals(cart.itemsPrice > 100 ? 0 : 100)
	const taxPrice = addDecimals(Number((0.15 * itemsPrice).toFixed(2)))
	const totalPrice = (
		Number(itemsPrice) +
		Number(shippingPrice) +
		Number(taxPrice)
	).toFixed(2)

	// const orderCreate = useSelector(state => state.orderCreate)
	// const { order, success, error } = orderCreate

	// useEffect(() => {
	// if (success) {
	// history.push(`/order/${order._id}`)
	// dispatch({ type: USER_DETAILS_RESET })
	// dispatch({ type: ORDER_CREATE_RESET })
	// }
	// // eslint-disable-next-line
	// }, [history, success])

	const placeOrderHandler = () => {
		// dispatch(
		// 	createOrder({
		// 		orderItems: cart.cartItems,
		// 		shippingAddress: cart.shippingAddress,
		// 		paymentMethod: cart.paymentMethod,
		// 		itemsPrice: cart.itemsPrice,
		// 		shippingPrice: cart.shippingPrice,
		// 		taxPrice: cart.taxPrice,
		// 		totalPrice: cart.totalPrice,
		// 	})
		// )
	}

	const fakePlaceOrder = () => {
		/* 
      first show spinner insdie place order btn
      then after 1sec show success msg 
      then redirect to homepage 
    */
		setLoading(true)
		setTimeout(() => {
			setSuccess(true)
			setLoading(false)
			dispatch(resetCart)
			setTimeout(() => {
				// redirect to homepage
				history.push('/')
			}, 900)
		}, 900)
	}

	return (
		<div className='page'>
			<div className='container'>
				<CheckoutSteps step1 step2 step3 step4 />
				<Row>
					<Col md={8}>
						<ListGroup variant='flush'>
							<ListGroup.Item>
								<h2>Shipping</h2>
								<p>
									<strong>Address:</strong>
									{cart.shippingAddress.address}, {cart.shippingAddress.city}{' '}
									{cart.shippingAddress.postalCode},{' '}
									{cart.shippingAddress.country}
								</p>
							</ListGroup.Item>

							<ListGroup.Item>
								<h2>Payment Method</h2>
								<strong>Method: </strong>
								{cart.paymentMethod}
							</ListGroup.Item>

							<ListGroup.Item>
								<h2>Order Items</h2>
								{cart.cartItems.length === 0 ? (
									<Message>Your cart is empty</Message>
								) : (
									<ListGroup variant='flush'>
										{cart.cartItems.map((item, index) => (
											<ListGroup.Item key={index}>
												<Row>
													<Col md={1}>
														<Image
															src={item.mainImage.imageUrl}
															alt={item.asing}
															fluid
															rounded
														/>
													</Col>
													<Col>
														<Link to={`/product/${item.product}`}>
															{item.title}
														</Link>
													</Col>
													<Col md={4}>
														{item.qty} x ${item.price} = $
														{item.qty * item.price}
													</Col>
												</Row>
											</ListGroup.Item>
										))}
									</ListGroup>
								)}
							</ListGroup.Item>
						</ListGroup>
					</Col>
					<Col md={4}>
						<Card style={{ border: '1px solid #dcdcdc' }}>
							<ListGroup variant='flush'>
								<ListGroup.Item>
									<h2>Order Summary</h2>
								</ListGroup.Item>
								<ListGroup.Item>
									<Row>
										<Col>Items</Col>
										<Col>${itemsPrice}</Col>
									</Row>
								</ListGroup.Item>
								<ListGroup.Item>
									<Row>
										<Col>Shipping</Col>
										<Col>${shippingPrice}</Col>
									</Row>
								</ListGroup.Item>
								<ListGroup.Item>
									<Row>
										<Col>Tax</Col>
										<Col>${taxPrice}</Col>
									</Row>
								</ListGroup.Item>
								<ListGroup.Item>
									<Row>
										<Col>Total</Col>
										<Col>${totalPrice}</Col>
									</Row>
								</ListGroup.Item>

								{/* {error && 	<ListGroup.Item><Message variant='danger'>{error}</Message>  	</ListGroup.Item> } */}

								<ListGroup.Item>
									<Button
										onClick={fakePlaceOrder}
										type='button'
										className='btn-block'
										disabled={cart.cartItems === 0}
										// onClick={placeOrderHandler}
									>
										Place Order
										{loading ? (
											<Spinner />
										) : success ? (
											<span className='green'>&#10004;</span>
										) : (
											''
										)}
									</Button>
								</ListGroup.Item>
							</ListGroup>
						</Card>
					</Col>
				</Row>
			</div>
		</div>
	)
}

export default PlaceOrderScreen
